@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes drop {
  0% {
    bottom: 0px;
    opacity: 1;
  }
  
  80% {
    opacity: 1;
  }
  
  100% {
    opacity: 1;
    bottom: -300px;
  }
}

@keyframes wave {
  0% {
    background-position: 0 160px;
    background-size: 170px 300px;
  }
  
  100% {
    background-position: 500px -18px;
    background-size: 250px 150px;
  }
}


.kuhl{
  margin-top: 5px;
  width: 88%;
}

.loader {
  width: 120px;
  height: 120px;
  position: fixed;
  margin: auto;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  text-align: center;
  line-height: 120px;
  font-family: sans-serif;
  color: #ffffff;
  font-size: 16px;
  border-radius: 25px;
}

.loader span {
  z-index: 3;
}

.loader-bg {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  border-radius: 25px;
  background: transparent repeat-x 0 00px/150px 300px url('data:image/png;base64,'); 
  z-index: 2;
  animation: wave 1s ease-out forwards;
}

.drops {
  -webkit-filter: url('#liquid');
  filter: url('#liquid');
  position: absolute;
  right: 0;
  left: 0;
  bottom: 0;
  z-index: 0 !important;
  opacity: 0;
  animation: fade-in .5s linear .6s forwards;
}

.drop1 , .drop2 {
  width: 21px;
  height: 24px;
  border-radius: 50%;
  position: absolute;
  z-index: 0;
  left: 0;
  right: 0;
  margin: auto;
  background-color: #226f74;
}

.drop1 {
  width: 20%;
  height: 16px;
  bottom: 0;
  border-radius: 0;

}

.drop2 {
  height: 30px;
  animation: drop 1.3s cubic-bezier(1,.19,.66,.12) .5s infinite;

}