.schnabulatorDiv{
  animation: Schnabulator 35s linear infinite ;
  animation-delay: 3s;
  position: fixed;
  right: -20%;
  user-select: none;
  width: 150px; 
  display: "none";
  z-index: 0;
  opacity: 0;
}

@keyframes Schnabulator {
  0% {
    bottom: 5%;
    right: -20%;
    opacity: 0;
    transform:  rotateY(0deg) ;
    -webkit-transform: rotateY(0deg) ;
  }
  1% {
    bottom: 5%;
    right: -20%;
    opacity: 1;
    transform:  rotateY(0deg) ;
    -webkit-transform: rotateY(0deg) ;
  }
  25% {
    bottom: 10%;
    opacity: 1;
    right: 7%;
    transform:  rotateY(0deg) ;
    -webkit-transform: rotateY(0deg) ;
  }
  35% {
    bottom: 10%;
    opacity: 1;
    right: 7%;
    transform:  rotateY(0deg) ;
    -webkit-transform: rotateY(0deg) ;

  }
  36% {
    bottom: 10%;
    opacity: 1;
    right: 7%;
    transform:  rotateY(-180deg) ;
    -webkit-transform: rotateY(-180deg) ;
  }
  50% {
    bottom: 30%;
    right: -20%;
    opacity: 1;
    transform:  rotateY(-180deg) ;
    -webkit-transform: rotateY(-180deg) ;
  }
  51% {
    bottom: 30%;
    right: -20%;
    opacity: 0;
    transform:  rotateY(-180deg) ;
    -webkit-transform: rotateY(-180deg) ;
  }

  100%{
    opacity: 0;
    bottom: 30%;
    right: -20%;
    transform:  rotateY(0deg) ;
    -webkit-transform: rotateY(0deg) ;
  }
}

.lillyDiv{
