    left: -20%;
    opacity: 1;
    transform:  rotateY(0deg) ;
  -webkit-transform: rotateY(0deg) ;
  }
  56%{
    opacity: 0;
    bottom: 30%;
    left: -20%;
    transform:  rotateY(0deg) ;
    -webkit-transform: rotateY(0deg) ;
  }

  100%{
    opacity: 0;
    bottom: 30%;
    left: -20%;
    transform:  rotateY(0deg) ;
    -webkit-transform: rotateY(0deg) ;
  }
}


@keyframes gradient {
  0% {
      background-position: 0% 50%;
  }
  50% {
      background-position: 100% 50%;
  }
  100% {
      background-position: 0% 50%;
  }
}


.navLink:hover{
  color: #226f74;
}

/*Hover*/
.hover-underline-animation {
  position: relative;
}

.hover-underline-animation:after {
  
  content: '';
  position: absolute;
  width: 100%;
  transform: scaleX(0);
  height: 2px;
  bottom: 0;
  left: 0;
  color: #226f74;
  background-color: #226f74;
  transform-origin: bottom right;
  transition: transform 0.2s ease-out;
}

.hover-underline-animation:hover:after {
  color: #226f74;
  transform: scaleX(1);
  transform-origin: bottom left;
}

.hover-underline-animation-yellow {
  position: relative;
}

br, span {
  user-select: none;
}

.hover-underline-animation-yellow:after {
  
  content: '';
  position: absolute;
  width: 100%;
  transform: scaleX(0);
  height: 2px;
  bottom: 0;
  left: 0;
  color: yellow;
  background-color: yellow;
  transform-origin: bottom right;
  transition: transform 0.2s ease-out;
}

.hover-underline-animation-yellow:hover:after {
  color: yellow;
  transform: scaleX(1);
  transform-origin: bottom left;
}


.loading:after {
  content: ' .';
  animation: dots 1s steps(5, end) infinite;}

@keyframes dots {
  0%, 20% {
    color: rgba(0,0,0,0);
    text-shadow:
      .25em 0 0 rgba(0,0,0,0),
      .5em 0 0 rgba(0,0,0,0);}
  40% {
    color: white;
    text-shadow:
      .25em 0 0 rgba(0,0,0,0),
      .5em 0 0 rgba(0,0,0,0);}
  60% {
    text-shadow:
      .25em 0 0 white,
      .5em 0 0 rgba(0,0,0,0);}
  80%, 100% {
    text-shadow:
      .25em 0 0 white,
      .5em 0 0 white;}}



